import React from 'react'
import { withState } from 'recompose'
import qs from 'query-string'
import { API_URL } from '../config'

const declineHandler = ({
  history,
  location,
  updateState,
}) => (e) => {
  e.preventDefault()
  const { declineUrl } = qs.parse(location.search)
  return fetch(`${API_URL}/applications/decline?declineUrl=${declineUrl}`)
    .then((res) => {
      if (res.ok) {
        updateState({ success: true })
      } else {
        updateState({ error: "Application was not found." })
      }
    })
    .catch((err) => updateState({ error: err }))
}

const SuccessPart = () => (
  <h4 className="mb-3">Your request has been successful!</h4>
)

const DefaultPart = ({ handleClick }) => (
  <div>
    <h4 className="mb-3">Are you sure you want to decline this application?</h4>
    <p>If you don't agree with the terms of this application, please ask your agent to resend.</p>
    <p>
      <button
        className="btn btn-danger pl-4 pr-4"
        onClick={handleClick}
        children="Yes, decline this application."
      />
    </p>
  </div>
)

const DeclinePage = ({
  history,
  location,
  state,
  updateState,
}) => (
  <div className="p-4">
    {state.success ? (
      <SuccessPart />
    ) : (
      <DefaultPart
        handleClick={state.error ? () => ({}) : declineHandler({
          history,
          location,
          updateState,
        })}
      />
    )}
    {state.error && (<p className="text-danger">{state.error}</p>)}
    <small className="text-secondary">This service is brought to you by <a href="https://www.applynowleasing.com">ApplyNowLeasing</a></small>
  </div>
)

export default withState('state', 'updateState', { success: false, error: undefined })(DeclinePage)


