import React from 'react'

const IMG_STYLE = ({ rounded }) => ({
  width: 160,
  borderRadius: rounded ? 80 : 0
})

const ReasonList = ({
  title,
  reasons,
  rounded,
}) => (
  <div className="loveus pt-5 pb-5 pl-2 pr-2">
    <div className="container">
      <h2
        className="text-white strokeme mt-1 mb-4 font-weight-bold text-center"
        children={title}
      />
      <div className="row">
        {reasons.map((r, i) => (
          <div key={i} className="d-flex col-12 col-sm-6 col-md-3 mt-4 mb-5 mb-md-0 justify-content-start flex-column">
            <img alt="" style={IMG_STYLE({ rounded })} className="bg-white border" src={r[0]} />
            <h4
              className="text-white strokeme mt-4"
              children={r[1]}
            />
            {r[2] && <p className="text-white mt-1">{r[2]}</p>}
          </div>
        ))}
      </div>
    </div>
  </div>
)

export default ReasonList


