import React from 'react'
import Form from '../Components/Form'
import signupForm from '../forms/signupForm'
import signupSubmit from '../handlers/signup'
import signupValidator from '../validators/signup-validator'

let newSignupForm = [[{ render: () => <h2 className="font-weight-bold mb-4 mt-4">Get Started. It's Free!</h2> }]].concat(signupForm.slice(1))

const Credit = ({ history }) => (
  <div>
    <Form
      className="container bg-light border p-large"
      validate={signupValidator}
      onSubmit={signupSubmit({ history })}
      fields={newSignupForm}
    />
  </div>
)
export default Credit


