import React from 'react'

import Appbar from '../Components/AppBar'
import Header from '../Components/Header'
import ReasonList from '../Components/ReasonList'
import Credits from '../Components/Credits'
import VideoHolder from '../Components/VideoHolder'
import Footer from '../Components/Footer'

const REASONS_1 = [
  ["/images/free.png", "Free", "No cost. We will always be 100% free to use for Realtors."],
  ["/images/intuitive.png", "Easy", "With ApplyNowLeasing.com, all you need is the applicant's name and email address. They run their own checks and pay application fees all online. There's no need for money orders."],
  ["/images/quick-and-easy.png", "Quick", "ApplyNowLeasing.com returns the results immediately as soon as the application is completed by the applicant."],
  ["/images/tailored-results.png", "Tailored Results", "ApplyNowLeasing.com was made specifically for Realtors to save you time and money. Let your applicants apply online."],
]

const REASONS_2 = [
  ["/images/background-check.png", "Background Check"],
  ["/images/credit-report.png", "Credit Report"],
  ["/images/pay-stubs.png", "Pay Stubs"],
  ["/images/photo-id.png", "Photo ID"],
]

const App = ({
  history
}) => (
  <div className="bg-white">
    <Appbar />
    <Header />
    <ReasonList
      title="Reasons Why You'll Love Us:"
      reasons={REASONS_1}
      rounded={true}
    />
    <VideoHolder />
    <ReasonList
      title="Our Reports Include:"
      reasons={REASONS_2}
    />
    <Credits
      history={history}
    />
    <Footer />
  </div>
)

export default App


